/* Global styles for the body */
body {
  font-family: Arial, sans-serif;
  background-color: #282c34; /* Set the background color to dark */
  color: white;              /* Set text color to white for contrast */
  margin: 0;
  padding: 0;
}

/* Main app container */
.App {
  text-align: center;
  margin: 0 auto;
  padding: 20px;
  max-width: 1200px;
  background-color: #282c34; /* Apply the background color here */
  color: white;              /* Set the text color to white */
  border-radius: 10px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

/* Navbar container */
.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #21252b; /* Darker background for the navbar */
}

/* Logo and hamburger */
.logo {
  font-size: 24px;
  color: white;
  cursor: pointer;
  display: none; /* Hidden by default, shown on mobile */
}

/* Hamburger icon */
.hamburger {
  font-size: 30px;
  display: block;
  color: white; /* Set the hamburger icon color */
}

/* Navbar links */
.navbar {
  display: flex;
  list-style-type: none;
  padding: 0;
  margin: 0;
  transition: all 0.3s ease;
}

.navbar li {
  margin: 0 15px;
}

.navbar a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
  padding: 10px;
}

.navbar a:hover {
  color: #00bfff; /* Light blue color on hover */
}

/* Mobile-specific styles */
@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    background-color: #21252b;
    width: 100%;
    height: 0;
    opacity: 0;
    overflow: hidden;
    transition: height 0.3s ease, opacity 0.3s ease;
  }

  .navbar.open {
    height: auto;
    opacity: 1;
  }

  /* Show the logo and hamburger on small screens */
  .logo {
    display: block;
  }
}

/* For larger screens (desktop), navbar links are displayed horizontally */
@media (min-width: 769px) {
  .navbar {
    display: flex;
  }

  .logo {
    display: none; /* Hide hamburger on desktop */
  }
}

/* Styling for the sections (NeoWs, Mars Rover, etc.) */
.neows-bar-chart,
.neows-pie-chart {
  max-width: 80%;  /* Limit the width to 80% of the container */
  margin: 20px auto;  /* Center the chart and add margin */
}

.nasa-library {
  text-align: center;
  padding: 20px;
}

.nasa-library-items {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.nasa-library-item {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 10px;
  text-align: center;
}

img, video {
  max-width: 100%;
  border-radius: 8px;
}

.video-container {
  display: none; /* Hide videos completely */
}

/* Spinner and error styles */
.spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.error-message {
  text-align: center;
  color: red;
  font-size: 1.2rem;
  margin-top: 20px;
}

.error-message button {
  padding: 10px 20px;
  font-size: 1rem;
  background-color: #ff0000;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.error-message button:hover {
  background-color: #cc0000;
}

/* Responsive styling */
@media (max-width: 768px) {
  .App-header {
    padding: 10px;
  }

  h1 {
    font-size: 1.8rem;
  }

  h2 {
    font-size: 1.6rem;
  }

  p {
    font-size: 1rem;
  }

  button {
    width: 100%;
    margin-top: 10px;
  }

  input[type="date"] {
    width: 100%;
    margin-bottom: 10px;
  }
}

